'use client';

import React from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { 
  Building2, Users, Receipt, 
  BarChart3, LogOut, HelpCircle,
  LayoutDashboard
} from 'lucide-react';
import { motion } from 'framer-motion';

const businessNavItems = [
  { icon: LayoutDashboard, label: 'Overview', path: '/business/dashboard' },
  { icon: Users, label: 'Team', path: '/business/team' },
  { icon: Receipt, label: 'Billing', path: '/business/billing' },
];

export default function BusinessLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();

  return (
    <div className="min-h-screen bg-slate-50">
      {/* Business Sidebar */}
      <div className="w-72 h-screen bg-zinc-900 border-r border-white/5 flex flex-col p-6 fixed left-0 top-0 z-50">
        <div className="flex items-center gap-3 mb-12">
          <img src="/Logo2.png" alt="Convivia24" className="w-8 h-8" />
          <span className="text-lg font-black tracking-tighter uppercase text-white">Convivia24 <span className="text-red-700 ml-1">Biz</span></span>
        </div>

        <nav className="flex-1 space-y-2">
          {businessNavItems.map((item) => {
            const isActive = pathname === item.path;
            return (
              <Link
                key={item.path}
                href={item.path}
                className={`group flex items-center gap-3 px-4 py-3 rounded-xl transition-all ${
                  isActive 
                    ? 'bg-red-700 text-white shadow-lg shadow-red-900/40' 
                    : 'text-zinc-500 hover:text-white hover:bg-white/5'
                }`}
              >
                <item.icon size={20} className={isActive ? 'text-white' : 'group-hover:text-red-700 transition-colors'} />
                <span className="text-sm font-bold tracking-tight uppercase">{item.label}</span>
                {isActive && (
                  <motion.div 
                    layoutId="activeBizNav"
                    className="ml-auto w-1 h-4 bg-white rounded-full"
                  />
                )}
              </Link>
            );
          })}
        </nav>

        <div className="pt-6 border-t border-white/5 space-y-2">
          <Link
            href="/support"
            className="flex items-center gap-3 px-4 py-3 rounded-xl text-zinc-500 hover:text-white transition-all"
          >
            <HelpCircle size={20} />
            <span className="text-sm font-bold tracking-tight uppercase">Support</span>
          </Link>
          <Link
            href="/"
            className="flex items-center gap-3 px-4 py-3 rounded-xl text-zinc-500 hover:text-red-600 transition-all"
          >
            <LogOut size={20} />
            <span className="text-sm font-bold tracking-tight uppercase">Exit Biz Portal</span>
          </Link>
        </div>
      </div>

      <main className="pl-72">
        <div className="p-10 max-w-7xl mx-auto">
          {children}
        </div>
      </main>
    </div>
  );
}
